<template>
      <div class="app-container">
      <el-form ref="form" :model="form" label-width="120px" label-position="left">
          <el-form-item label="IP地址">
            <el-input v-model="form.ip"></el-input>
          </el-form-item>
          <el-form-item label="备注">
            <el-input v-model="form.note"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="addWhiteList">提交</el-button>
          </el-form-item>
      </el-form>
  </div>
</template>
<script>
import { addWhiteListItem } from '@/api/login';

export default {
  data() {
    return {
      add_time: '',
      form: {
        ip: '',
        note: ''
      }
    };
  },
  methods: {
    addWhiteList() {
      this.add_time = new Date().getTime();
      var data = {
        add_time: this.add_time,
        adder: this.$store.getters.name,
        data: this.form
      };
      addWhiteListItem(data).then(response => {
        console.log(response);
        if (response.Status === 200) {
          this.$notify({
            title: '添加成功',
            message: '白名单添加成功',
            type: 'success'
          });
          this.$router.push({ path: 'whitelist' });
        } else {
          console.log(response);
        }
      });
    }
  }
};
</script>



